"use client";

import { apiRequest } from '../../client';
import FloatingAlert from '../../../components/ui/alert';

async function fetchData() {
  try {
    const data = await apiRequest('https://afuoaqahz7.execute-api.eu-west-2.amazonaws.com/dev/boards/Board-af39ae0d-82e2-4d66-bc70-6e3d10585d0a');
    console.log(data);
  } catch (error) {
    console.error('Failed to fetch data:', error);
  }
}

export default function Page() {
  return (
    <div>
      <button 
        onClick={fetchData} 
        className="px-4 py-2 bg-blue-500 text-white font-semibold rounded-lg shadow-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-400 focus:ring-opacity-75"
      >
        Fetch Data
      </button>

      {/* Alert with manual close */}
      <FloatingAlert type="info" message="This is an informational message!" />

      {/* Alert with auto-dismiss after 5 seconds */}
      <FloatingAlert type="success" message="Operation successful!" duration={5000} />

      {/* Error alert with auto-dismiss after 3 seconds */}
      <FloatingAlert type="error" message="Something went wrong!" duration={3000} />
      
    </div>
  );
}